<template>
  <div class="d-flex flex-no-wrap flex-column align-center justify-center" style="width:100%">
    <v-card width="90%" max-width="800px" :loading="loading">
      <div
        class="d-flex flex-no-wrap justify-space-between"
        :class="$vuetify.breakpoint.smAndDown ? 'flex-column' : ''"
      >
        <div
          class="d-flex flex-no-wrap justify-start align-center card"
          :class="$vuetify.breakpoint.smAndDown ? 'flex-column' : ''"
        >
          <v-avatar class="ma-3" size="80">
            <v-img  :src="foto"></v-img>
            <!-- <img width="50px" :src="foto" /> -->
          </v-avatar>
          <div>
            <v-card-title class="headline">{{name}}</v-card-title>
            <v-card-subtitle>
              <div class="d-flex flex-no-wrap flex-column align-start justify-center">
                <span  class="subtitle-1">{{cargo}}</span>
                <div class="d-flex flex-no-wrap align-start justify-center">
                  <span style="padding-right:5px" class="caption font-weight-medium">Banco</span>
                  <span style="padding-right:5px" class="caption">{{banco}}</span>
                  <span style="padding-right:5px" class="caption font-weight-medium">Agência</span>
                  <span class="caption">{{agencia}}</span>
                </div>
                <v-divider vertical class="mx-4"></v-divider>
                <div class="d-flex flex-no-wrap align-start justify-center">
                  <span class="overline font-weight-medium">Data de Admissão:</span>
                  <span class="overline">{{dataadm}}</span>
                </div>
              </div>
            </v-card-subtitle>
          </div>
        </div>
        <v-card-actions>
          <div style="width:100%;padding-right:7px" class="d-flex flex-no-wrap justify-center">
            <v-btn
              :disabled="loading"
              outlined
              @click="contrataCandidato($event)"
              color="green darken-1 accent-4"
            >Enviar para o EC</v-btn>
          </div>
        </v-card-actions>
      </div>
      <v-alert
        v-model="successAlert"
        type="success"
        dismissible
        @input="removeCandidato()"
      >Candidato {{this.name}} enviado para o EC com sucesso</v-alert>
      <v-alert v-model="errorAlert" :type="type" dismissible>
        <div class="d-flex flex-no-wrap align-center justify-space-between">
          <span>Erro ao enviar o candidato {{this.name}} para o EC{{erroMsg}}</span>
          <v-btn @click="dialogDetalhes = true" v-if="btnDetalhes">Detalhes</v-btn>
        </div>
      </v-alert>
    </v-card>
    <v-dialog v-model="dialogDetalhes" width="500">
      <v-card>
        <v-card-text>{{textDetalhes}}</v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialogDetalhes = false">Fechar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "Main",
  props: [
    "name",
    "cargo",
    "dataadm",
    "foto",
    "position",
    "position_number",
    "banco",
    "agencia"
  ],
  data: () => ({
    loading: false,
    successAlert: false,
    erroMsg: "",
    dialogDetalhes: false,
    btnDetalhes: false,
    textDetalhes: "",
    type: "error",
    errorAlert: false
  }),
  methods: {
    removeCandidato() {
      this.$store.dispatch("removeCandidato", this.position);
    },
    contrataCandidato(event) {
      event.preventDefault();

      this.loading = true;

      var data = {
        position: this.position,
        position_number: this.position_number,
        name: this.name
      };

      //"https://cors-anywhere.herokuapp.com/" +
      this.$axios
        .post(this.$endpoint.cpi + "/http/cba/contrataCandidato", data, {
          //headers: { 'APIKey': 'mIsueqoWs8tEBGc3DvJ5AIMwSGQMbPIX' }
          headers: { 'APIKey': this.$endpoint.APIKey }
        })
        .then(response => this.apiSuccess(response))
        .catch(error => this.apiError(error));

      return false;
    },
    apiSuccess(response) {
      console.log(response.data);
      this.erroMsg = "";
      this.loading = false;
      if (response.data.status === "OK") this.successAlert = true;
      else {
        if (response.data.msg) {
          if (response.data.msg.length < 200) {
            this.erroMsg = " - " + response.data.msg;
            this.btnDetalhes = false;
          } else {
            this.textDetalhes = response.data.msg;
            this.btnDetalhes = true;
          }
        }

        if (response.data.status === "WAR") this.type = "warning";
        else this.type = "error";

        this.errorAlert = true;
      }
    },
    apiError(error) {
      this.type = "error";
      this.erroMsg = "";
      this.loading = false;
      this.errorAlert = true;
    }
  }
};
</script>

<style scoped>
.card {
  padding-left: 15px;
  padding-right: 15px;
}
</style>


