<template>
  <div class="overflow-hidden d-flex justify-center">
    <div style="width: 100%">
      <!--<v-app-bar color="#0000bf" dense dark scroll-target="#zcontent">-->
      <v-app-bar color="#FFFFFF" dense scroll-target="#zcontent">
        <div style="width: 100%" class="d-flex justify-center">
          <div style="max-width: 1280px; width: 100%" class="d-flex" color="secundary">

            <img
              height="42px"
              v-if="$vuetify.breakpoint.mdAndUp"
              src="../assets/CBA_logotipo.png"
            /> 

            <v-spacer/>
            <v-toolbar-title style="" v-if="$vuetify.breakpoint.mdAndUp">Contratações pendentes Vs. 1.0</v-toolbar-title>
            <v-spacer/>
            
            <!--
            <img
              height="42px"
              v-if="$vuetify.breakpoint.mdAndUp"
              src="../assets/logo-juntos-somos-mais.png"
            /> 
            --> 

            <v-divider
              v-if="$vuetify.breakpoint.mdAndUp"
              vertical
              class="mx-4"
            ></v-divider>

            <!--
            <img
              v-if="$vuetify.breakpoint.mdAndUp"
              class="acesso_logo"
              src="../assets/novo-logo-acesso-rh.svg"
            />
            -->
            <!--
            <v-text-field
              rounded
              v-model="search"
              @input="filter()"
              class="text-green"
              dense="#0000FF"
              hide-details
              prepend-inner-icon="mdi-magnify"
              label="Buscar"
              color="#0000FF"
              single-line
              outlined
            ></v-text-field>
            -->
            <v-text-field
              rounded
              v-model="search"
              @input="filter()"
              class="text-green"
              dense="#0000FF"
              hide-details
              prepend-inner-icon="mdi-magnify"
              label="Buscar"
              color="#0000FF"
              single-line
              outlined
            ></v-text-field>
            <v-spacer></v-spacer>
            <!--<img
              height="42px"
              v-if="$vuetify.breakpoint.mdAndUp"
              src="../assets/votorantim_new_logo_white.png"
            />
            <img
              height="42px"
              v-if="$vuetify.breakpoint.smAndDown"
              src="../assets/votorantim_white.svg"
            />-->
            <!--  <v-img :src="require('../assets/CBA_LOGO.png')" width="0.1px"></v-img> -->
<!--
            <img
              height="42px"
              v-if="$vuetify.breakpoint.mdAndUp"
              src="../assets/venergia2.png"
            />
-->

            <img
              height="42px"
              v-if="$vuetify.breakpoint.mdAndUp"
              src="../assets/unicopeople.png"
            />

          </div>
        </div>
      </v-app-bar>
      <v-sheet
        id="zcontent"
        class="overflow-y-auto"
        style="background: #eceff1; height: 100vh"
      >
        <v-container fluid>
          <br />
          <!-- v-for="candidato in this.candidatosEncontrados" -->
          <div class="d-flex flex-column align-center flex-no-wrap">
            <!--ejs-listbox :dataSource="dadosEmpresa()"></ejs-listbox-->
            Empresa:
            <v-select
              @change="call_api($event)"
              :items="chaves"
              label="Filtro de Empresa"
              outlined
              item-text="Descricao"
              item-value="Company"
              single-line
            >
            </v-select>
          </div>
          <div
            class="d-flex flex-column align-center flex-no-wrap"
            v-if="!loading && $vuetify.breakpoint.mdAndUp"
          >
            <card-candidato
              class="card_candidatos"
              v-for="candidato in this.$store.getters.candidatos"
              :key="candidato.name"
              :name="candidato.name"
              :cargo="candidato.cargo"
              :dataadm="candidato.dataadm"
              :foto="candidato.foto"
              :position_number="candidato.position_number"
              :position="candidato.position"
              :banco="candidato.banco"
              :agencia="candidato.agencia"
            />
            <br />
            <v-btn @click="buscaCandidatos()" v-if="verMais">VER MAIS</v-btn>
            <br />
            <br />
            <br />
          </div>
          <div
            class="d-flex flex-column align-center flex-no-wrap"
            v-if="!loading && $vuetify.breakpoint.smAndDown"
          >
            <card-candidato-mobile
              class="card_candidatos"
              v-for="candidato in this.$store.getters.candidatos"
              :key="candidato.name"
              :name="candidato.name"
              :cargo="candidato.cargo"
              :dataadm="candidato.dataadm"
              :foto="candidato.foto"
              :position_number="candidato.position_number"
              :position="candidato.position"
              :banco="candidato.banco"
              :agencia="candidato.agencia"
            />
            <br />
            <v-btn @click="buscaCandidatos()" v-if="verMais">VER MAIS</v-btn>
            <br />
            <br />
            <br />
          </div>
          <div
            class="d-flex flex-column align-center flex-no-wrap"
            v-if="loading"
          >
            <v-skeleton-loader
              class="card_skeleton"
              width="80%"
              max-width="800px"
              type="list-item-avatar-three-line"
              ref="skeleton"
            ></v-skeleton-loader>
            <v-skeleton-loader
              class="card_skeleton"
              width="80%"
              max-width="800px"
              type="list-item-avatar-three-line"
              ref="skeleton"
            ></v-skeleton-loader>
            <v-skeleton-loader
              class="card_skeleton"
              width="80%"
              max-width="800px"
              type="list-item-avatar-three-line"
              ref="skeleton"
            ></v-skeleton-loader>
          </div>
        </v-container>
      </v-sheet>
    </div>

    <v-dialog v-model="erroDialog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">Erro de Conexão</v-card-title>
        <v-card-text>{{ erroText }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="erroDialog = false"
            >Fechar</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import anonymousProfile from "../assets/profile.svg";
import cardCandidato from "../components/CardCandidato.vue";
import cardCandidatoMobile from "../components/CardCandidatoMobile.vue";

export default {
  name: "Main",
  components: {
    "card-candidato": cardCandidato,
    "card-candidato-mobile": cardCandidatoMobile,
  },

  data: () => ({
    search: "",
    loading: false,
    erroDialog: false,
    erroText: "",
    candidatosEncontrados: [],
    verMais: false,
    chaves: [],
    company: "1001",
  }),
  methods: {
    filter() {
      this.$store.dispatch("filterCandidatos", this.search);
    },
    //Melhoria busca dados por empresa : 22/03/2022

    call_api(e) {

//alert("alterado!" + e);
console.log(this.$store.getters.candidatos);
this.$store.getters.candidatos.splice(0);

this.loading = true;
this.$axios
      .get(this.$endpoint.cpi + "/http/cba/buscaCandidatosConcluidos" + "?" + "companyCode=" + e, {
        // headers: { APIKey: "mIsueqoWs8tEBGc3DvJ5AIMwSGQMbPIX" },
        headers: { APIKey: this.$endpoint.APIKey },
      })
      .then((response) => this.apiSuccess(response))
      .catch((error) => this.apiError(error));

    },

    buscadadosEmpresa() {
      //"https://cors-anywhere.herokuapp.com/"+
      this.$axios
        .get(
          //this.$endpoint.fiori + "/ZET_VSAHR_BuscaEmpresaSet",
          this.$endpoint.fiori + "/ZET_CBAHR_BuscaEmpresaSet",
          {
            //withCredentials: true,
            //headers: {
            //  'Access-Control-Allow-Origin': 'http://localhost:8080'
            //}
            //'X-CSRF-Token': 'Fetch'
            //  auth: {
            //  username: "tjuliocst",
            //   password: "xxxxxx" // Bad password
            //  }
          }
        )
        .then((result) => {
          //let chaves = this.getEmpresa(result)
          let array = this.getEmpresa(result);

          //console.log(array)

          if (array.length > 0) {
            array.forEach((empresa) => {
              let values = [];
              values.Company = empresa.Company;
              values.Descricao = empresa.Company + " - " + empresa.Description;

              this.chaves.push(values);
            });

            console.log("chaves>");
            console.log(this.chaves);
          }
        })
        .catch((error) => {
          console.log("envio gateway error?!" + error);
          this.apiError(error);
        });
    },
    buscaCandidatos() {

      this.loading = true;
      //"https://cors-anywhere.herokuapp.com/" +
      this.$axios
        .get(this.$endpoint.cpi + "/http/cba/buscaCandidatosConcluidos", {
          headers: {
            //APIKey: "mIsueqoWs8tEBGc3DvJ5AIMwSGQMbPIX",
            headers: { APIKey: this.$endpoint.APIKey },
            skip: this.$store.getters.candidatos.length.toString(),
          },
        })
        .then((response) => this.apiSuccess(response))
        .catch((error) => this.apiError(error));
    },
    getEmpresa(response) {
      return response.data.d.results;

      //  if (response.data.d.results.length > 0) {
      //    response.data.d.results.forEach(empresa => {

      //        array = {};
      //        array.Company = empresa.Company;
      //        array.Descrição = empresa.Description;
      //        chaves.push(array);

      //   });

      //  }
    },
    apiSuccess(response) {
      this.loading = false;
      if (response.data.length % 6 !== 0) this.verMais = false;
      else this.verMais = true;

      if (response.data.length === 0) this.verMais = false;

      if (response.data.length > 0) {
        response.data.forEach((candidato) => {
          let foto;
          if (candidato.foto != "NO")
            foto = "data:image/jpg;base64," + candidato.foto;
          else foto = anonymousProfile;

          let data = {
            name: candidato.nome,
            dataadm: candidato.dataadm,
            cargo: candidato.cargo,
            foto: foto,
            banco: candidato.banco,
            agencia: candidato.agencia,
            position: candidato.position,
            position_number: candidato.position_number,
          };
          this.$store.dispatch("incluiCandidato", data);
        });
      }
    },
    apiError(error) {
      this.loading = false;
      this.erroText = error;
      this.erroDialog = true;
    },
  },
  mounted() {
    this.loading = true;

    //"https://cors-anywhere.herokuapp.com/" + // 3O9RxTXBPOCFtbdiceHRxlHX7jQ2eVAk

    this.buscadadosEmpresa();

    this.$axios
      .get(this.$endpoint.cpi + "/http/cba/buscaCandidatosConcluidos", {
        //headers: { APIKey: "mIsueqoWs8tEBGc3DvJ5AIMwSGQMbPIX" },
        headers: { APIKey: this.$endpoint.APIKey },
      })
      .then((response) => this.apiSuccess(response))
      .catch((error) => this.apiError(error));
  },
  resetInput(){
    this.this.$store.getters.candidatosEncontrados = "";
    this.this.$store.getters.candidatos = "";
  }
};
</script>

<style scoped>
.acesso_logo {
  background: #cfd8dc;
  border-radius: 6px;
  padding: 5px;
}

.card_candidatos {
  margin-bottom: 16px;
}

.card_skeleton {
  margin-top: 35px;
}
</style>


