import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    filtered: false,
    gxCandidatosEncontrados: [],
    gxCandidatosEncontradosAux: []
  },
  getters:{
    candidatos: state => {
      return state.gxCandidatosEncontrados
    }
  },
  mutations: {
    setCandidatos(state,candidato){
      state.gxCandidatosEncontrados.push(candidato)
    },
    filterCandidatos(state,filterValue){
      if(!state.filtered){
        state.gxCandidatosEncontradosAux = state.gxCandidatosEncontrados.slice()
        state.filtered = true
      }

      state.gxCandidatosEncontrados = state.gxCandidatosEncontradosAux.slice()

      if(filterValue === ''){
        state.filtered = false
        return
      }

      for (var index = 0; index < state.gxCandidatosEncontrados.length; index++) { 
        if(!state.gxCandidatosEncontrados[index].name.toLowerCase().includes(filterValue)){
          state.gxCandidatosEncontrados.splice(index,1)
          index--
        }
      } 
    },
    removeCandidato(state,position){
      for (var index = 0; index < state.gxCandidatosEncontrados.length; index++) { 
        if(state.gxCandidatosEncontrados[index].position.indexOf(position) !== -1){
          state.gxCandidatosEncontrados.splice(index,1)
          index--
        }
      }
      if(gxCandidatosEncontradosAux.length > 0) 
      for (var i = 0; i < state.gxCandidatosEncontradosAux.length; i++) { 
        if(state.gxCandidatosEncontradosAux[i].position.indexOf(position) !== -1){
          state.gxCandidatosEncontradosAux.splice(i,1)
          i--
        }
      } 
    }
  },
  actions: {
    removeCandidato(commit,position){
      this.commit("removeCandidato",position)
    },
    incluiCandidato(commit,candidato){
       this.commit("setCandidatos",candidato)
    },
    filterCandidatos(commit,filterValue){
      this.commit("filterCandidatos",filterValue)
   }
  },
  modules: {
  }
})



